    padding-right: 150px ;
}
.headerOne li{
    display: flex;
    margin-top: 5px;
    font-family:Helvetica ,Helvetica,Arial,sans-serif;
    font-size: 13px;
    margin-left: 8px;
    margin-bottom: 13px;
    color: #3483FA;
}

